'use strict';
function getPersistence(num) {
  debugger;
  let numToString = String(num);
  let current = 1;
  let counter = 0;
  while (numToString.length > 1) {
    for (let i = 0; i < numToString.length; i++) {
      current *= numToString[i];
      counter++;
  }

    numToString = String(current);
  current = 1
  }
  return console.log(counter);
}

getPersistence(397);
